/**
 * System imports
 ============================================================================ */
import React from "react";
import { Form, InputGroup, Button } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";

/**
  * User imports
  ============================================================================ */
import SubmitButton from "../submitButton";
import Card from "../card";
import ModalMessage from "../modalMessage";
import { useStickyState } from "../../utils/state";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    namespace: "TanInput",
    request: "submit tan",
    show: false,
    size: "lg",
  }; // TODO: Adjust this

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { tanRequired, userData } = props.barrelGetters;
  const { setTanRequired } = props.barrelSetters;
  const { sendRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]); // adjust namespace

  /** 
    * Yup schema
    ============================================================================ */
  const schema = yup.object({
    tan: yup.string().required(t("TAN is required")),
  });

  /**
    * Functions
    ============================================================================ */
  const onSubmit = (data) => {
    data.email = userData.email;
    // console.log(data);
    return sendRequest({ req: props.request, data });
  };

  /**
    * Form
    ============================================================================ */
  const ComponentForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <>
      <div>
        {t("A transaction number has been sent to your email account.")}
      </div>
      <br />
      <Form noValidate onSubmit={handleSubmit}>
        <Form.Group controlId="validationFormikTAN">
          <Form.Label>{t("TAN")}</Form.Label>
          <InputGroup>
            <Form.Control
              type="string"
              placeholder={""}
              aria-describedby="inputGroupPrepend"
              name="tan"
              value={values.tan}
              onChange={handleChange}
              isInvalid={!!errors.tan && touched.tan}
            />
            <Form.Control.Feedback type="invalid">
              {errors.tan}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        <SubmitButton
          {...args}
          caption={t("Submit")}
          request={props.request}
          namespace={props.namespace}
        />
      </Form>
    </>
  );

  const message = (
    <Formik
      component={ComponentForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        tan: "",
      }}
    />
  );

  /**
    * Formik return
    ============================================================================ */
  return (
    <>
      <ModalMessage
        message={message}
        show={tanRequired}
        size={props.size}
        centered={true}
        onHide={() => setTanRequired(false)}
      />
    </>
  );
};
