/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";

/**
  * User imports
  ============================================================================ */
import Card from "../card";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Checkout" }; // TODO: adjust namespace

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    externalRequest,
    accountNumber,
    cartContent,
    cartPurposes,
  } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { sendRequest, format } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const Cart = () => {
    let priceTotal = 0;
    let priceReal = 0;

    return (
      <>
        {cartContent !== null &&
          cartContent.map((element, key) => {
            priceTotal += element.price * element.amount;
            let product_found = "N";

            if (cartPurposes !== null) {
              let product = cartPurposes.find(
                (products) => products.purpose_id === element.product
              );

              if (typeof product !== "undefined") {
                product_found = "Y";
                priceReal += element.price * element.amount;
              }
              // console.log("product:", product);
            }

            //   if (typeof product !== "undefined") {
            //     product_found = product.title;
            //   }
            // }
            return (
              <div key={key}>
                <div>product: {element.product}</div>
                <div>price: {element.price}</div>
                <div>amount: {element.amount}</div>
                <div>found: {product_found}</div>
                <br />
              </div>
            );
          })}

        <div>Total: {format(priceTotal)}</div>
        <div>Real: {format(priceReal)}</div>
      </>
    );
  };

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    // const cart = typeof externalRequest.cart ? externalRequest.cart : "";
    // <div>External request: {JSON.stringify(externalRequest)}</div>
    return (
      <Card title={t("Checkout")}>
        <div>
          <Cart />
        </div>
      </Card>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
