/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
import {
  Accordion,
  Card,
  Tab,
  Row,
  Col,
  ListGroup,
  // Nav,
  Form,
  // Toast,
  // Badge,
  // Container,
  // Button,
} from "react-bootstrap";
// import { useAccordionToggle } from "react-bootstrap/AccordionToggle";
import { useTranslation } from "react-i18next";
// import Octicon, { Plus, PlusSmall, Gear } from "@primer/octicons-react";
import { Add, Extension, Store, Edit, Close } from "@material-ui/icons";

/**
  * User imports
  ============================================================================ */
// import Dropzone from "../dropzone";
import BusinessRegistration from "../businessRegistration";
import PurposeRegistration from "../purposeRegistration";
import PurposeProperty from "../purposeProperty";
import ShareholderRegistration from "../shareholderRegistration";
// import shareholderRegistration from "../shareholderRegistration";
import Shareholder from "../shareholder";
import Pagination from "../pagination";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "MyBusiness" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    userBusinesses,
    countBusinesses,
    businessesPerPage,
    activePageBusinesses,
    userBusinessesLoaded,
    activeBusiness,
    activeBusinessID,
    businessPurposes,
    countBusinessPurposes,
    businessPurposesPerPage,
    activePageBusinessPurposes,
    activeBusinesPurpose,
    activePurposeProperty,
    purposeShareholders,
    // purposeID,
    activePurposeData,
    language,
    accountNumber,
  } = props.barrelGetters;
  const {
    setRedirectTo,
    setBusinessPurposes,
    setActiveBusinesPurpose,
    setActivePurposeProperty,
    // setPurposeID,
    setActivePurposeData,
  } = props.barrelSetters;
  const {
    getBusinesses,
    getBusinessPurposes,
    getPurposeShareholders,
    sendRequest,
  } = props.barrelFunctions;
  // const {  } = props.barrelEventHandlers;

  const { t } = useTranslation([props.namespace]);

  // useEffect(() => {
  //   // getBusinesses();
  //   // getBusinesses();
  //   // eslint-disable-next-line
  // }, []);

  /**
   * Toggle business (Accordion card)
   * @param {*} param0
   */
  const ToggleBusiness = ({ eventKey, businessID }) => {
    getBusinessPurposes(businessID, eventKey, 1);
  };

  /**
   * Toggle purpose (Accordion card)
   * @param {*} param0
   */
  const TogglePurpose = ({ eventKey, purposeID }) => {
    getPurposeShareholders(purposeID, eventKey);
  };

  /**
   * Create:
   * - business menu
   * - business purposes menu
   */
  let menuItems = [];
  let purposes = [];
  let countShareholders = 0;
  userBusinesses !== null &&
    Object.keys(userBusinesses).map((business, keyBusiness) => {
      menuItems[keyBusiness] = [];
      purposes[keyBusiness] = [];
      businessPurposes !== null &&
        Object.keys(businessPurposes).map((purpose, keyPurpose) => {
          if (
            businessPurposes[purpose].business_id ===
            userBusinesses[business].business_id
          ) {
            menuItems[keyBusiness][keyPurpose] = (
              <ListGroup.Item
                action
                key={businessPurposes[purpose].purpose_id}
                eventKey={businessPurposes[purpose].purpose_id}
                onClick={() =>
                  TogglePurpose({
                    eventKey: keyPurpose,
                    purposeID: businessPurposes[purpose].purpose_id,
                  })
                }
              >
                <span style={{ whiteSpace: "nowrap" }}>
                  <Extension style={{ color: "var(--dark)" }} />{" "}
                  {businessPurposes[purpose].title}
                </span>
                <span style={{ float: "right" }}>
                  <Edit
                    onClick={(e) => {
                      // setPurposeID(businessPurposes[purpose].purpose_id);
                      // console.log("edit:", businessPurposes[purpose]);

                      setActivePurposeData(
                        JSON.stringify(businessPurposes[purpose])
                      );
                      setActiveBusinesPurpose("addPurpose");
                      e.stopPropagation();
                    }}
                    fontSize="small"
                    style={{ color: "var(--dark)", cursor: "pointer" }}
                  />
                </span>
              </ListGroup.Item>
            );

            purposes[keyBusiness][keyPurpose] = (
              <Tab.Pane
                key={businessPurposes[purpose].purpose_id}
                eventKey={businessPurposes[purpose].purpose_id}
              >
                <Accordion
                  defaultActiveKey={
                    activePurposeProperty !== null
                      ? activePurposeProperty
                      : "shareholders"
                  }
                >
                  {/* Purpose ID */}
                  <Card bg="light">
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{ cursor: "pointer" }}
                      eventKey="purpose_id"
                      onClick={() => setActivePurposeProperty("purpose_id")}
                    >
                      {t("Purpose ID")}
                    </Accordion.Toggle>
                    <Accordion.Collapse eventKey="purpose_id">
                      <Card.Body>
                        <Card.Text>
                          {businessPurposes[purpose].purpose_id}
                        </Card.Text>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>

                  {/* Purpose title */}
                  <Card bg="light">
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{ cursor: "default" }}
                      eventKey="title"
                      onClick={() => setActivePurposeProperty("title")}
                    >
                      <span style={{ cursor: "pointer" }}>
                        {t("Purpose Title")}
                      </span>
                    </Accordion.Toggle>

                    <Accordion.Collapse eventKey="title">
                      <Card.Body>
                        <Card.Text>{businessPurposes[purpose].title}</Card.Text>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>

                  {/* Purpose description */}
                  <Card bg="light">
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{ cursor: "default" }}
                      eventKey="description"
                      onClick={() => setActivePurposeProperty("description")}
                    >
                      <span style={{ cursor: "pointer" }}>
                        {t("Purpose Description")}
                      </span>
                    </Accordion.Toggle>

                    <Accordion.Collapse eventKey="description">
                      <Card.Body>
                        <Card.Text>
                          {businessPurposes[purpose].description}
                        </Card.Text>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>

                  {/* Purpose shareholders */}
                  <Card bg="light">
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{ cursor: "default" }}
                      eventKey="shareholders"
                      onClick={() => setActivePurposeProperty("shareholders")}
                    >
                      <span style={{ cursor: "pointer" }}>
                        {t("Purpose Shareholders")}
                      </span>
                      <span style={{ float: "right" }}>
                        <Add
                          onClick={(e) => {
                            setActivePurposeProperty("addShareholder");
                            e.stopPropagation();
                          }}
                          fontSize="small"
                          style={{ color: "var(--dark)", cursor: "pointer" }}
                        />
                      </span>
                    </Accordion.Toggle>

                    {/* Toggle: Add shareholder */}
                    <Accordion.Collapse eventKey="addShareholder">
                      <Card.Body>
                        <ShareholderRegistration
                          {...args}
                          purposeID={businessPurposes[purpose].purpose_id}
                        />
                      </Card.Body>
                    </Accordion.Collapse>

                    {/* Toggle: Shareholders list */}
                    <Accordion.Collapse eventKey="shareholders">
                      <Card.Body>
                        {typeof purposeShareholders !== "undefined" &&
                          purposeShareholders !== null &&
                          Object.keys(purposeShareholders).map(
                            (shareholder, keyShareholder) => {
                              if (
                                purposeShareholders[shareholder].purpose_id ===
                                businessPurposes[purpose].purpose_id
                              ) {
                                const { purpose_id } = purposeShareholders[
                                  shareholder
                                ];
                                const {
                                  shareholder_id,
                                  firstname,
                                  lastname,
                                  // email,
                                  roles,
                                  share,
                                } = purposeShareholders[shareholder];

                                countShareholders++;

                                const arrRoles =
                                  roles !== null ? roles.split(",") : [];

                                return (
                                  <Shareholder
                                    {...args}
                                    key={keyShareholder}
                                    id={keyShareholder}
                                    shareholder={shareholder_id}
                                    purpose={purpose_id}
                                    firstname={firstname}
                                    lastname={lastname}
                                    share={share}
                                    roles={arrRoles}
                                  />
                                );
                              } else {
                                return ""; // TODO: check if this works
                              }
                            }
                          )}
                        {countShareholders === 0 && (
                          <Card.Text className="text-muted">
                            {t("No shareholders")}
                          </Card.Text>
                        )}

                        {/* End shareholders list */}
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>

                  {/* Product status */}
                  <Card bg="light">
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{ cursor: "pointer" }}
                      eventKey="productStatus"
                      onClick={() => setActivePurposeProperty("productStatus")}
                    >
                      {t("Product Status")}
                    </Accordion.Toggle>
                    <Accordion.Collapse eventKey="productStatus">
                      <Card.Body
                        onClick={(e) => {
                          console.log("Click", e);

                          const bp = businessPurposes;
                          bp[purpose].status =
                            businessPurposes[purpose].status === 0 ? 1 : 0;
                          setBusinessPurposes(JSON.stringify(bp));

                          let data = {};
                          // data.business_id =
                          //   businessPurposes[purpose].business_id;
                          // data.language = language;
                          data.purpose_id =
                            businessPurposes[purpose].purpose_id;
                          // data.purpose_title = businessPurposes[purpose].title;
                          data.purpose_status =
                            businessPurposes[purpose].status;
                          return sendRequest({
                            req: "change purpose status",
                            data,
                          });
                        }}
                      >
                        <Form>
                          {typeof purposeShareholders === "undefined" ||
                            (purposeShareholders === null && (
                              <Form.Text>
                                {t(
                                  "To publish, please add at least one shareholder"
                                )}
                                <br />
                                <br />
                              </Form.Text>
                            ))}
                          <Form.Check
                            type="switch"
                            disabled={
                              typeof purposeShareholders === "undefined" ||
                              purposeShareholders === null
                            }
                            label={t(
                              businessPurposes[purpose].status === 0
                                ? "Not published"
                                : "Published"
                            )}
                            checked={businessPurposes[purpose].status === 1}
                            onChange={(e) => {
                              console.log(e);
                            }}
                          />
                        </Form>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>

                  {/* DEL: Add shareholder*/}
                  {false && (
                    <Card bg="light">
                      <Accordion.Toggle
                        as={Card.Header}
                        style={{ cursor: "pointer" }}
                        eventKey="addShareholder"
                        onClick={() =>
                          setActivePurposeProperty("addShareholder")
                        }
                      >
                        <span style={{ whiteSpace: "nowrap" }}>
                          <Add
                            fontSize="small"
                            style={{ color: "var(--dark)" }}
                          />
                          &nbsp;{t("Add Shareholder")}
                        </span>
                      </Accordion.Toggle>
                      {/* Toggle */}
                    </Card>
                  )}
                </Accordion>
              </Tab.Pane>
            );
          }
          countShareholders = 0;
          return true;
        });
      return true;
    });

  const jsx = (
    <>
      <Accordion
        defaultActiveKey={
          activeBusiness !== null || !userBusinessesLoaded
            ? activeBusiness
            : "myBusinesses"
        }
      >
        {/* Add business location top */}

        {/* List user businesses */}
        {userBusinesses !== null &&
          Object.keys(userBusinesses).map((business, keyBusiness) => {
            const date = new Date(userBusinesses[business].created);
            const businessSince = new Intl.DateTimeFormat(language).format(
              date
            );
            // keyBusiness[3] = userBusinesses[business].business_id;
            return (
              <Card key={keyBusiness}>
                <Accordion.Toggle
                  as={Card.Header}
                  eventKey={keyBusiness}
                  style={{ cursor: "pointer" }}
                  onClick={() =>
                    ToggleBusiness({
                      eventKey: keyBusiness,
                      businessID: userBusinesses[business].business_id,
                    })
                  }
                >
                  <Store style={{ color: "var(--dark)" }} />{" "}
                  {userBusinesses[business].title}
                </Accordion.Toggle>
                <Accordion.Collapse eventKey={keyBusiness}>
                  <Card.Body>
                    <Tab.Container
                      id="left-tabs-example"
                      defaultActiveKey={
                        activeBusinesPurpose !== null
                          ? activeBusinesPurpose
                          : "aboutBusiness"
                      }
                    >
                      <Row>
                        {/* Layout: Left side - Menu */}
                        <Col sm={5}>
                          <ListGroup variant="flush">
                            {/* Menu item: About business */}
                            <ListGroup.Item action eventKey="aboutBusiness">
                              <span style={{ whiteSpace: "nowrap" }}>
                                {t("About")}
                              </span>
                              <span style={{ float: "right" }}>
                                <Add
                                  onClick={(e) => {
                                    // setPurposeID(null);
                                    setActivePurposeData(null);
                                    setActiveBusinesPurpose("addPurpose");
                                    e.stopPropagation();
                                  }}
                                  fontSize="small"
                                  style={{
                                    color: "var(--dark)",
                                    cursor: "pointer",
                                  }}
                                />
                              </span>
                            </ListGroup.Item>

                            {/* Menu item: Add purpose */}
                            {false && (
                              <ListGroup.Item action eventKey="addPurpose">
                                <span style={{ whiteSpace: "nowrap" }}>
                                  <Add
                                    fontSize="small"
                                    style={{ color: "var(--dark)" }}
                                  />
                                  &nbsp;{t("Add Purpose")}
                                </span>
                              </ListGroup.Item>
                            )}

                            {/* Menu items: Registered purposes */}
                            {menuItems[keyBusiness]}

                            {/* old add purpose */}
                          </ListGroup>
                          {countBusinessPurposes !== null && (
                            <>
                              <br />
                              <Pagination
                                {...args}
                                size="sm"
                                countItems={countBusinessPurposes}
                                itemsPerPage={businessPurposesPerPage}
                                active={activePageBusinessPurposes}
                                onClick={(number) =>
                                  getBusinessPurposes(
                                    activeBusinessID,
                                    0,
                                    number
                                  )
                                }
                              />
                            </>
                          )}
                        </Col>

                        {/* Layout: Right side - Content */}
                        <Col sm={7}>
                          <Tab.Content>
                            <Tab.Pane eventKey="aboutBusiness">
                              <Accordion defaultActiveKey="businessID">
                                {/* Business logo */}

                                {/*
                              <Card>
                                <Accordion.Toggle
                                  as={Card.Header}
                                  style={{ cursor: "pointer" }}
                                  eventKey="businessLogo"
                                >
                                  [LOGO]
                                </Accordion.Toggle>
                                <Accordion.Collapse eventKey="businessLogo">
                                  <Card border="light">
                                    <Card.Body>
                                      <Dropzone data={args} />
                                    </Card.Body>
                                  </Card>
                                </Accordion.Collapse>
                              </Card>
                              */}

                                {/* Business ID */}
                                <Card>
                                  <Accordion.Toggle
                                    as={Card.Header}
                                    style={{ cursor: "pointer" }}
                                    eventKey="businessID"
                                  >
                                    {t("Business ID")}
                                  </Accordion.Toggle>
                                  <Accordion.Collapse eventKey="businessID">
                                    <Card border="light">
                                      <Card.Body>
                                        <Card.Text>
                                          {userBusinesses[business].business_id}
                                        </Card.Text>
                                      </Card.Body>
                                    </Card>
                                  </Accordion.Collapse>
                                </Card>

                                {/* Business description */}
                                <Card>
                                  <Accordion.Toggle
                                    as={Card.Header}
                                    style={{ cursor: "pointer" }}
                                    eventKey="businessDescription"
                                  >
                                    {t("Business Description")}
                                  </Accordion.Toggle>
                                  <Accordion.Collapse eventKey="businessDescription">
                                    <Card border="light">
                                      <Card.Body>
                                        <Card.Text>
                                          {userBusinesses[business].description}
                                        </Card.Text>
                                      </Card.Body>
                                    </Card>
                                  </Accordion.Collapse>
                                </Card>

                                {/* Business Creation Date */}
                                <Card>
                                  <Accordion.Toggle
                                    as={Card.Header}
                                    style={{ cursor: "pointer" }}
                                    eventKey="businessCreationDate"
                                  >
                                    {t("Business Created")}
                                  </Accordion.Toggle>
                                  <Accordion.Collapse eventKey="businessCreationDate">
                                    <Card border="light">
                                      <Card.Body>
                                        <Card.Text>{businessSince}</Card.Text>
                                      </Card.Body>
                                    </Card>
                                  </Accordion.Collapse>
                                </Card>
                              </Accordion>
                            </Tab.Pane>

                            {/* Start Business purposes */}
                            {purposes[keyBusiness]}
                            {/* End Business purposes */}

                            {/* Add new purpose */}
                            <Tab.Pane eventKey="addPurpose">
                              <Accordion defaultActiveKey="newPurpose">
                                <Card>
                                  <Accordion.Toggle
                                    as={Card.Header}
                                    eventKey="newPurpose"
                                  >
                                    {t(
                                      activePurposeData === null
                                        ? "New Purpose"
                                        : "Edit Purpose"
                                    )}
                                  </Accordion.Toggle>
                                  <Accordion.Collapse eventKey="newPurpose">
                                    <Card.Body>
                                      <PurposeRegistration
                                        {...args}
                                        businessID={
                                          userBusinesses[business].business_id
                                        }
                                        id={
                                          activePurposeData !== null
                                            ? activePurposeData.id
                                            : null
                                        }
                                        purposeID={
                                          activePurposeData !== null
                                            ? activePurposeData.purpose_id
                                            : null
                                        }
                                        title={
                                          activePurposeData !== null
                                            ? activePurposeData.title
                                            : ""
                                        }
                                        description={
                                          activePurposeData !== null
                                            ? activePurposeData.description
                                            : ""
                                        }
                                      />
                                    </Card.Body>
                                  </Accordion.Collapse>
                                </Card>
                              </Accordion>
                            </Tab.Pane>
                          </Tab.Content>
                        </Col>
                      </Row>
                    </Tab.Container>
                    {/* pagination prev */}
                  </Card.Body>
                </Accordion.Collapse>
              </Card>
            );
            // console.log("BUSINESS:", userBusinesses[business]);
          })}

        {/* Get business */}

        {false && userBusinesses === null && (
          <Card>
            <Accordion.Toggle
              as={Card.Header}
              eventKey="myBusinesses"
              onClick={getBusinesses}
              style={{ cursor: "pointer" }}
            >
              <span>{t("My Businesses")}</span>
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="myBusinesses">
              <Card.Body>{t("No businesses")}</Card.Body>
            </Accordion.Collapse>
          </Card>
        )}

        {/* add business location bottom */}

        {/* Add business */}
        <Card>
          <Accordion.Toggle
            as={Card.Header}
            eventKey="addBusiness"
            style={{ cursor: "pointer" }}
          >
            <span>
              <Add fontSize="small" style={{ color: "var(--dark)" }} />
              &nbsp;
              {t("Add Business")}
            </span>
          </Accordion.Toggle>
          <Accordion.Collapse eventKey="addBusiness">
            <Card.Body>
              <BusinessRegistration {...args} />
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
      {userBusinesses !== null && (
        <>
          <br />

          <Pagination
            {...args}
            size="md"
            countItems={countBusinesses}
            itemsPerPage={businessesPerPage}
            active={activePageBusinesses}
            onClick={(number) => getBusinesses(number)}
          />
        </>
      )}
    </>
  );

  if (accountNumber === null) setRedirectTo("/sign-out");

  return <>{jsx}</>;
};
