import { sendData } from "../utils/sockets";

export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {};

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);
  const { req, data, session, uid, barrels } = props;

  /**
   * Barrels
   ============================================================================ */
  const { debug, userData, userSequence } = barrels.barrelGetters;
  const {
    setUserData,
    setUID,
    setAccountNumber,
    // setRequest,
    // setUserBusinesses,
  } = barrels.barrelSetters;
  const { updateLastSeenUser } = barrels.barrelFunctions;
  //   const {} = barrels.barrelEventHandlers;

  let requestFound = false;

  /** 
   * Request event dispatcher
   ============================================================================ */
  const onRequest = (req, callback) => {
    if (requestFound) return;
    if (req === props.req || req === "default") {
      callback();
      requestFound = true;
    }
  };

  /** 
   * Request handlers
  ============================================================================ */

  onRequest("sign in user", () => {
    setUserData(JSON.stringify(data));
    sendData({ req, data, uid, session }, debug);
  });

  onRequest("get user sequence", () => {
    const dt = new Date(data.birthDate);
    const fullyear = dt.getFullYear().toString();
    const month = (dt.getMonth() + 1).toString().padStart(2, "0");
    const day = dt.getDate().toString().padStart(2, "0");

    data.birthDate = `${fullyear}-${month}-${day}`;

    sendData({ req, data, uid, session }, debug);
  });

  onRequest("register account", () => {
    const dt = new Date(data.birthDate);
    const fullyear = dt.getFullYear().toString();
    const year = fullyear.slice(-2);
    const month = (dt.getMonth() + 1).toString().padStart(2, "0");
    const day = dt.getDate().toString().padStart(2, "0");
    const birthDate = `${day}${month}${year}`;
    const timezone = data.timezone.slice(0, 2);
    const initial = userData.lastname.slice(0, 1).toUpperCase();
    const icode = initial.charCodeAt(0).toString().padStart(5, "0");

    data.scode = `${timezone}${birthDate}${initial}`;
    data.tcode = `${timezone}${birthDate}${icode}`;
    data.timezone = data.timezone.slice(5);
    data.sequence = userSequence.toString().padStart(5, "0");
    data.birthDate = `${fullyear}-${month}-${day}`;

    setUID(data.personalID);
    sendData({ req, data, uid, session }, debug);
  });

  onRequest("default", () => {
    sendData({ req, data, uid, session }, debug);

    return true;
  });

  // onRequest("unknown", () => {
  //   showMessage(`UNKNOWN REQUEST ${req}`, "info");

  //   return true;
  // });

  return requestFound;
};
