import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {};

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Custom hooks
   */
  const { t } = useTranslation("Logout");

  /**
   * Barrels
   */
  const { error, language } = props.barrelGetters;
  const { setLanguage } = props.barrelSetters;
  const { showMessage } = props.barrelFunctions;
  const { onLogOut } = props.barrelEventHandlers;

  useEffect(() => {
    /**
     * Save vars
     */
    const tempError = error;
    const tempLanguage = language;

    /**
     * Log out
     */
    onLogOut();

    /**
     * Restore vars
     */
    setLanguage(tempLanguage);

    if (tempError !== null) {
      showMessage(tempError, "error");
    }

    // eslint-disable-next-line
  }, []); // logOut

  return (
    <>
      <h4>{t("See you soon")}</h4>
      <hr />

      <br />
      <p className="text-center">
        <a href="/">{t("Sign in")}</a>
      </p>
    </>
  );
};
