/**
 * CSS
 */
import "bootstrap/dist/css/bootstrap.min.css";
import "./Main.css";

/**
 * System imports
 */
import React, { useEffect } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";

// import { useTranslation } from "react-i18next";
// import moment from "moment";

/**
 * User imports
 */
import Summary from "../summary";
import {
  useSocket,
  sendData,
  startSession,
  endSession,
} from "../../../utils/sockets";
// import { useEventListener } from "../../../utils/useEventListener";
// import { useEventListener } from "../../../utils/useEventListener";
import { useInterval } from "../../../utils/useInterval";
import Login from "../../login";
import Logout from "../../logout";
import SignUp from "../../signup";
import PasswordReset from "../../passwordReset";

import Alert from "../../alert";
import LanguageSelector from "../../language-selector";
import ValidateEmail from "../../validateEmail";
import ChangePassword from "../../changePassword";
import Auth from "../../auth";
// import { useEventListener } from "../../../utils/useEventListener";

/**
 * Component: Main
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Main" };

  /**
      * Properties
      ============================================================================ */
  const props = Object.assign(defaults, args);
  const barrels = args;

  /**
      * Barrels
      ============================================================================ */
  const {
    session,
    instance,
    debug,
    signedIn,
    accountNumber,
    externalRequest,
    cartContent,
    validateTokenEmail,
    passwordResetTokenValidated,
    validateTokenPasswordReset,
    connected,
  } = props.barrelGetters;
  const { setUpdateInterval } = props.barrelSetters;
  const {
    processResponse,
    getIP,
    getCart,
    getCartPurposes,
    updateLastSeenUser,
    updateInterval,
    getAccountData,
    sendRequest,
    sendEmailValidationRequest,
    sendPasswordResetRequest,
    getContainerClass,
    getRedirect,
  } = props.barrelFunctions;
  const { onRedirect } = props.barrelEventHandlers;

  /** 
   * Custom hooks
   ============================================================================ */

  useSocket((data) => {
    processResponse({
      args: data,
      barrels: barrels,
    });
  });

  useInterval(() => {
    updateLastSeenUser();
  }, updateInterval);

  /** 
   * Effect hooks
   ============================================================================ */

  /**
   * On mount / on unmount
   */
  useEffect(() => {
    getIP();

    getCart();

    return () => {
      // on unmount
    };

    // eslint-disable-next-line
  }, []);

  /**
   * On session ID change
   */
  useEffect(() => {
    if (session === null) return;

    let data = {};
    data.request = "hello";
    data.instance = instance;
    startSession(data, session, debug);

    // eslint-disable-next-line
  }, [session]);

  /**
   * On connected
   */
  useEffect(() => {
    // eslint-disable-next-line
  }, [connected]);

  /**
   * On signed in status change
   */
  useEffect(() => {
    if (!signedIn) return;

    setUpdateInterval(-1);
    getAccountData();

    // eslint-disable-next-line
  }, [signedIn]);

  /**
   * On account number change
   */
  useEffect(() => {
    if (accountNumber === null) return;

    // eslint-disable-next-line
  }, [accountNumber]);

  /**
   * On external request
   */
  useEffect(() => {
    if (externalRequest === null) return;

    const data = {
      customer: accountNumber,
      cart: externalRequest.cart,
    };

    sendRequest({ req: "get cart", data: data });

    // eslint-disable-next-line
  }, [JSON.stringify(externalRequest)]);

  /**
   * On cart content change
   */
  useEffect(() => {
    if (cartContent === null) return;

    getCartPurposes();

    // eslint-disable-next-line
  }, [JSON.stringify(cartContent)]);

  /**
   * On email validation token
   */
  useEffect(() => {
    if (validateTokenEmail === null) return;

    sendEmailValidationRequest(JSON.parse(validateTokenEmail));

    // eslint-disable-next-line
  }, [validateTokenEmail]);

  /**
   * On email validation token
   */
  useEffect(() => {
    if (passwordResetTokenValidated === null) return;

    onRedirect("/password");

    // eslint-disable-next-line
  }, [passwordResetTokenValidated]);

  /**
   * On password reset validation token
   */
  useEffect(() => {
    if (validateTokenPasswordReset === null) return;

    sendPasswordResetRequest(JSON.parse(validateTokenPasswordReset));

    // eslint-disable-next-line
  }, [validateTokenPasswordReset]);

  const { wrapperClass, innerClass } = getContainerClass(signedIn);

  const appClass = signedIn ? "App" : "App bg";

  return (
    <>
      <Router>
        {getRedirect()}

        <div className={appClass} style={{}}>
          {/**
           * Wrapper for the main window
           */}
          <div className={wrapperClass} style={{}}>
            {/**
             * Top margin of the main window (only for >= sm)
             */}
            <div className="d-none d-md-block" style={{ height: "1rem" }}></div>

            {/**
             * The main window
             */}
            <div className={innerClass}>
              {/**
               * Top padding of the main window
               */}
              <div style={{ height: "16px" }}></div>

              {/**
               * The alert
               */}
              <div>
                <Alert data={barrels} />
              </div>

              {/**
               * Routes
               */}
              <Switch>
                {/**
                 * Route: Home
                 */}
                <Route
                  exact
                  path="/"
                  component={() => {
                    return signedIn ? Summary(barrels) : Login(barrels);
                  }}
                />

                {/**
                 * Route: Sign in
                 */}
                <Route
                  exact
                  path="/sign-in"
                  component={() => {
                    return Login(barrels);
                  }}
                />

                {/**
                 * Route: Sign up
                 */}
                <Route
                  exact
                  path="/sign-up"
                  component={() => {
                    return SignUp(barrels);
                  }}
                />

                {/**
                 * Route: Sign up
                 */}
                <Route
                  exact
                  path="/auth"
                  component={() => (signedIn ? Auth(barrels) : Login(barrels))}
                />

                {/**
                 * Route: Validate mail
                 */}
                <Route
                  exact
                  path="/validate-email"
                  component={() => {
                    return ValidateEmail(barrels);
                  }}
                />

                {/**
                 * Route: Change password
                 */}
                <Route
                  exact
                  path="/password"
                  component={() => {
                    return ChangePassword(barrels);
                  }}
                />

                {/**
                 * Route: User profile
                 */}
                <Route
                  exact
                  path="/profile"
                  component={() => {
                    return signedIn ? Summary(barrels) : Login(barrels);
                  }}
                />

                {/**
                 * Route: Currency
                 */}
                <Route
                  exact
                  path="/currency"
                  component={() => {
                    return signedIn ? Summary(barrels) : Login(barrels);
                  }}
                />

                {/**
                 * Route: Sign out
                 */}
                <Route
                  exact
                  path="/sign-out"
                  component={() => {
                    return Logout(barrels);
                  }}
                />

                {/**
                 * Route: Password reset
                 */}
                <Route
                  exact
                  path="/reset"
                  component={() => {
                    return PasswordReset(barrels);
                  }}
                />

                {/**
                 * Route: Account registration
                 */}
                <Route
                  exact
                  path="/account-registration"
                  component={() => {
                    return Summary(barrels);
                  }}
                />

                {/**
                 * Route: Business registration
                 */}
                <Route
                  exact
                  path="/business-registration"
                  component={() => {
                    return Summary(barrels);
                  }}
                />

                {/**
                 * Route: My business
                 */}
                <Route
                  exact
                  path="/my-business"
                  component={() => {
                    return Summary(barrels);
                  }}
                />

                {/**
                 * Route: Balance
                 */}
                <Route
                  exact
                  path="/balance"
                  component={() => {
                    return Summary(barrels);
                  }}
                />

                {/**
                 * Route: Transfer
                 */}
                <Route
                  exact
                  path="/transfer"
                  component={() => {
                    return Summary(barrels);
                  }}
                />

                {/**
                 * Route: Summary
                 */}
                <Route
                  exact
                  path="/summary"
                  component={() => {
                    return Summary(barrels);
                  }}
                />
              </Switch>

              {/**
               * Language selector
               */}
              <div className="text-center">
                <LanguageSelector {...barrels} />
              </div>
            </div>
          </div>
        </div>
      </Router>
    </>
  );
};
