  /**
   * Sends requests to the server
   * @param {string} request Request name
   * @param {object} data Request data
   */
  const sendRequest = ({ req, data = null }) => {
    if (processRequest({ req, data, session, uid: "STA", barrels })) {
      // request processed successfully
      return true;
    } else {
      console.log("THIS SHOULD NEVER BE REACHED");
      return sendData({ req, uid: "STA", session, data }, debug);
    }
  };

  /**
   * Processes user request
   * @param {*} param0
   */
  const processRequest = ({ req, data, session, uid, barrels }) => {
    data.instance = instance;

    if (req !== "update last seen user") {
      setRequest(req);
    }

    return requestHandler({
      req,
      data,
      session,
      uid,
      barrels,
    });
  };

  /**
   * Processes server response
   *
   * Chooses a handler depending on response status:
   * Error or other (success | continue | finished | pending etc.)
   *
   * @param {*} param0
   */
  const processResponse = ({ args, barrels }) => {
    return args.status === "error"
      ? errorHandler({
          args: args,
          uid: uid,
          barrels: barrels,
        })
      : responseHandler({
          args: args,
          uid: uid,
          barrels: barrels,
        });
  };

