  //   // setAccountNumber(data.personalID);
  //   return sendRequest({ req: "register account", data });
  // };

  /**
   * On transfer type change
   * @param {string} id The transfer type id ("U2S", "U2U", "U2B" etc.)
   */
  const onTransferTypeChange = (id) => {
    // console.log("setTransferType: " + id);
    setTransferType(id);
  };

  const onTransferSuccess = () => {
    // console.log("ON TRANSFER SUCCESS", barrels);
  };

  /**
   * On transfer
   * @param {object} data Transfer data
   */
  const onTransfer = (data) => {
    // console.log("ON TRANSFER (data):", data);
    // sendRequest({ req: "register account", data: data });
    sendRequest(data);
  };

  // const onBusinessRegistration = (data) => {
  //   // console.log("ON BUSINESS REGISTRATION (data):", data);
